.main {
	display: flex;
	flex-direction: column;

	padding-bottom: rem(74);

	@include min540 {
		padding-bottom: rem(24);
	}

	&__top {
		margin-top: rem(32);
		margin-bottom: rem(53);

		margin-left: rem(32);
		margin-right: rem(36);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min540 {
			margin-top: rem(15);
			margin-bottom: rem(20);

			justify-content: center;
		}

		.left {
			display: flex;
			align-items: center;

			.logo {
				margin-right: rem(12);
			}

			.locale {
				font-weight: 300;
			}
		}

		.right {

			@include min540 {
				display: none;
			}

			
		}
	}

	&__center {
		display: flex;
		// flex-wrap: wrap;
		justify-content: space-between;

		@include min1050 {
			flex-wrap: wrap;
		}

		.left {
			margin-left: rem(80);

			@include min1050 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-left: 0;

				display: flex;
				flex-direction: column;
				align-items: center;

			}

			&__button {
				display: none;
				margin-bottom: rem(26);

				width: 100%;

				padding-left: rem(36);
				padding-right: rem(36);

				.button_blue, .button_green {
					width: 100%;
				}

				@include min540 {
					display: flex;
					flex-direction: column;
				}

				.button_blue {
					margin-bottom: rem(18);
					height: rem(42);
					padding: 0 rem(20);
				}
			}

			.title {
				color: #15457C;
				font-size: rem(48);
				font-weight: bold;
				margin-bottom: rem(12);

				@include min768 {
					font-size: rem(24);
					margin-bottom: rem(7);
				}
			}

			.subtitle {
				color: #4D4D4D;
				font-size: rem(24);
				font-weight: 300;
				margin-bottom: rem(72);

				@include min1050 {
					margin-bottom: rem(38);
				}

				@include min768 {
					font-size: rem(14);
				}
			}

			.items {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				flex: 0 0 rem(600);
				max-width: rem(600);

				@include min1050 {
					flex: 0 0 auto;
					max-width: 100%;
					
					justify-content: center;
				}

				.item {
					display: flex;
					align-items: center;


					margin-bottom: rem(40);

					@include min1050 {
						flex: 0 0 100%;
						max-width: 100%;
						margin-bottom: rem(24);
						justify-content: center;
					}

					&__image {
						display: flex;
						align-items: center;
						justify-content: center;

						margin-right: rem(20);

						flex: 0 0 rem(45);
						max-width: rem(45);
					}

					&__text {
						max-width: rem(206);
						display: flex;
						flex-direction: column;

						b {
							font-size: rem(18);
							color: #4D4D4D;
							margin-bottom: rem(3);
						}

						p {
							font-weight: 300;
							color: #4D4D4D;
						}
					}
				}
			}
		}

		.right {
			margin-right: rem(52);

			flex: 0 0 rem(369);
			max-width: rem(369);

			display: flex;
			align-items: center;
			flex-direction: column;

			@include min1050 {
				flex: 0 0 calc(100% - 20px);
				max-width: calc(100% - 20px);
				margin-right: 0;

				margin-left: rem(10);
				margin-right: rem(10);

				display: flex;
				justify-content: center;
			}

			.form__bottom {

				background: linear-gradient(180deg, #FFFFFF 0%, #E9E8E8 100%);
				border-radius: 10px;

				padding: rem(4) rem(30);

				span {
					font-size: rem(12);
					font-weight: 300;
					color: #4D4D4D;
				}

				@include min1050 {
					display: none;
				}
			}

			.form {
				padding: rem(42) rem(38) rem(36) rem(38);
				background: #FFFFFF;
				box-shadow: 3px 5px 6px rgba(0, 0, 0, 0.15);
				border-radius: 13px;

				display: flex;
				flex-direction: column;
				align-items: center;

				margin-bottom: rem(16);

				@include min1050 {
					padding: rem(32) rem(24);
					width: 100%;
					margin-bottom: 0;
					max-width: rem(540);
				}

				&__title {
					margin-bottom: rem(30);

					@include min1050 {
						margin-bottom: rem(24);
					}

					span {
						font-size: rem(18);
						color: #15457C;
						font-weight: bold;
					}
				}

				.field-set {
					display: flex;
					flex-direction: column;
					margin-bottom: rem(18);
					width: 100%;

					@include min1050 {
						margin-bottom: rem(14);
					}

					label {
						font-weight: 300;
						color: #4D4D4D;
						margin-bottom: rem(10);

						@include min1050 {
							margin-bottom: rem(7);
						}
					}

					.field {
						width: 100%;
						background-color: #F1F1F1;
						border-radius: rem(10);
						color: #282828;
						font-size: rem(18);
						font-weight: bold;
						padding: rem(8) rem(14);

						@include placeholder {
							color: #282828;
						}

						@include min1050 {
							padding: rem(5) rem(12);
						}
					}

					.field-control {
						border-radius: rem(10);
						overflow: hidden;
						padding: rem(5) rem(20);
						display: flex;
						justify-content: space-between;
						align-items: center;

						background-color: #F1F1F1;

						@include min1050 {
							padding: rem(2) rem(12);
							margin-bottom: rem(6);
						}

						button {
							font-weight: bold;
							font-size: rem(24);
							color: #4D4D4D;
						}

						.count {
							background-color: #FBFBFB;
							border-radius: rem(10);
							height: rem(36);
							width: rem(142);

							text-align: center;
							font-weight: bold;
							font-size: rem(18);
							color: #282828;
							// -webkit-appearance: none;

							-moz-appearance: textfield;

							@include min1050 {
								width: rem(114);
								height: rem(28);
							}

							&::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
								-webkit-appearance: none;
							}

							@include placeholder {
								color: #282828;
							}
						}
					}
				}

				.field-button {
					display: flex;
					flex-direction: column;
					width: 100%;

					.button_red {
						margin-bottom: rem(14);
						width: 100%;
						background: linear-gradient(180deg, #E3722C 0%, #C33734 100%);
						box-shadow: 4px 3px 6px rgba(0, 0, 0, 0.15);
						border-radius: 25px;

						display: flex;
						justify-content: center;
						align-items: center;
						height: rem(42);

						span {
							font-weight: bold;
							color: white;
						}
					}

					
				}
			}
		}
	}

}

.main__foot {
		// position: absolute;
		// bottom: 0;
		// left: 0;
		margin-top: auto;
		width: 100%;
		background-color: #F0F0F0;
		padding: rem(2) rem(14);

		@include min1050 {
			display: none;
		}

		span {
			font-size: rem(12);
			font-weight: 300;
			color: #4D4D4D;
		}
	}

.button_blue {
						background: linear-gradient(180deg, #54AAD6 0%, #3D7EBE 100%);
						box-shadow: 4px 3px 6px rgba(0, 0, 0, 0.15);
						border-radius: 25px;

						display: flex;
						justify-content: center;
						align-items: center;
						height: rem(42);

						span {
							font-weight: bold;
							color: white;
						}
					}

.button_green {
				background: linear-gradient(126.15deg, #40FB48 11.56%, #2DCF34 88.44%);
				box-shadow: 4px 3px 6px rgba(0, 0, 0, 0.15);
				border-radius: 25px;

				padding: rem(9) rem(18);
				
				display: flex;
				align-items: center;

				@include min540 {
					padding: 0 rem(20);
					height: rem(42);
					justify-content: center;
				}

				.icon {
					margin-right: rem(13);
				}

				.text {
					font-size: rem(18);
					font-weight: bold;
					color: white;
				}
			}